.form__text,
.form__desc {
  background-color: $basic-extra-grey;
  border: 4px solid transparent;

  &:hover {
    background-color: $special-grey-3;
  }

  &:focus {
    background-color: $basic-white;
    outline: 4px solid $special-grey-3;
  }
}

.form__heading {
  padding-bottom: 20px;
  padding-top: 18px;
  padding-left: 2px;
  text-align: center;

  @include font-Heading-tiny;

  @media (min-width: $tablet-width) {
    @include font-Heading-small;

    padding-bottom: 55px;
  }

  &--effect {
    padding-bottom: 15px;

    @media (min-width: $tablet-width) {
      padding-bottom: 55px;
      padding-left: 59px;
      text-align: start;
    }

    @media (min-width: $desktop-width) {
      padding: 15px 0 60px 89px;
    }
  }

  &--present {
    @media (min-width: $tablet-width) {
      text-align: start;
      padding-left: 56px;
    }

    @media (min-width: $desktop-width) {
      padding-left: 79px;
    }
  }
}

.form__subtitle {
  @include font-Text-large;

  padding-right: 15px;

  @media (min-width: $desktop-width) {
    padding-right: 15px;
  }

  &--hidden {
    padding-right: 0;

    @media (min-width: $desktop-width) {
      display: none;
    }
  }
}

.form__text,
.form__grade,
.form__desc {
  @include font-Heading-tiny;
  text-transform: none;
}

.form__subtitle--desc {
  padding-top: 9px;
  padding-bottom: 7px;
  padding-left: 45px;

  @media (width: $tablet-width) {
    padding-left: 82px;
  }

  @media (min-width: $desktop-width) {
    padding-left: 0;
    padding-right: 19px;
  }
}

.form__subtitle--mail {
  @media (width: $tablet-width) {
    margin-top: 18px;
  }
}

.form__grade {
  padding-top: 2px;

  &--park {
    padding-top: 0;
  }

  &--rock {
    padding-top: 0;
  }

  @media (min-width: $tablet-width) {
    padding-top: 0;
  }
}

.form__text--tel,
.form__text--email {
  padding-bottom: 14px;
  margin: 3px 20px;

  @media (min-width: $tablet-width) {
    margin: 0 60px;
  }

  @media (min-width: $desktop-width) {
    margin: 0;
  }
}

.form__radio {
  position: absolute;
  z-index: -1;
  opacity: 0;
}

.form__radio + label {
  display: inline-flex;
  align-items: center;
  user-select: none;
  padding-left: 3px;

  @media (min-width: $tablet-width) {
    padding-left: 10px;
  }
}

.form__radio + label::before {
  content: "";
  display: inline-block;
  width: 18px;
  height: 18px;
  flex-shrink: 0;
  flex-grow: 0;
  border-radius: 50%;
  border: 20px solid $basic-extra-grey;
  background-color: $basic-extra-grey;
}

.form__radio:hover + label::before,
.form__radio:focus + label::before {
  border: 20px solid $special-grey-3;
  background-color: $special-grey-3;
}

.form__radio:active + label::before {
  border: 20px solid $special-grey-4;
  background-color: $special-grey-4;
}

.form__radio:active:checked + label::before {
  border: 20px solid $special-grey-4;
  background-color: $special-black;
}

.form__radio:checked + label::before {
  background-color: $basic-black;
}

.form__checkbox {
  position: absolute;
  z-index: -1;
  opacity: 0;
}

.form__checkbox + label {
  display: inline-flex;
  align-items: center;
  user-select: none;
}

.form__checkbox:checked + label::before,
.form__checkbox + label::before {
  content: "";
  display: inline-block;
  width: 56px;
  height: 56px;
  flex-shrink: 0;
  flex-grow: 0;
  background-color: $basic-extra-grey;
  border-radius: 5px;
  background-repeat: no-repeat;
  background-position: center center;
  background-size: 50% 50%;
}

.form__checkbox:hover + label::before,
.form__checkbox:focus + label::before {
  background-color: $special-grey-3;
}

.form__checkbox:active + label::before {
  background-color: $special-grey-4;
}

.form__checkbox:checked:active + label::before {
  background-color: $special-grey-4;
  background-image: url("data:image/svg+xml,%3Csvg width='24' height='17' viewBox='0 0 24 17' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath opacity='0.3' d='M22.925 1.325L21.825 0.225C21.525 -0.075 20.925 -0.075 20.625 0.225L8.725 12.125L2.525 5.925C2.225 5.625 1.625 5.625 1.325 5.925L0.225 7.025C-0.075 7.325 -0.075 7.925 0.225 8.225L8.025 16.025C8.225 16.225 8.425 16.225 8.625 16.225C8.825 16.225 9.025 16.125 9.225 16.025L22.725 2.525C23.225 2.225 23.225 1.725 22.925 1.325Z' fill='%23333333'/%3E%3C/svg%3E%0A");
}

.form__checkbox:checked + label::before {
  background-image: url("data:image/svg+xml,%3Csvg width='24' height='17' viewBox='0 0 24 17' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M22.925 1.325L21.825 0.225C21.525 -0.075 20.925 -0.075 20.625 0.225L8.725 12.125L2.525 5.925C2.225 5.625 1.625 5.625 1.325 5.925L0.225 7.025C-0.075 7.325 -0.075 7.925 0.225 8.225L8.025 16.025C8.225 16.225 8.425 16.225 8.625 16.225C8.825 16.225 9.025 16.125 9.225 16.025L22.725 2.525C23.225 2.225 23.225 1.725 22.925 1.325Z' fill='black'/%3E%3C/svg%3E");
}

.form__rating {
  padding-left: 26px;
  margin-bottom: -3px;

  @media (min-width: $tablet-width) {
    padding-left: 26px;
  }

  @media (min-width: $desktop-width) {
    padding-left: 18px;
    margin-bottom: 4px;
  }
}

.form__rating--lions {
  @media (min-width: $desktop-width) {
    padding-left: 26px;
    margin-bottom: 5px;
  }
}

.form__rating--park {
  margin-top: 3px;
}

.form__rating--rock {
  margin-top: 6px;

  @media (min-width: $tablet-width) {
    margin-top: 0;
  }
}

.form__button {
  @include font-Heading-small;

  color: $basic-white;
  background-color: $basic-blue;
  border: none;
  padding: 18px 33px 14px 32px;
  margin: 1px 23px 43px 23px;
  width: 280px;

  &:hover {
    background-color: $special-blue-1;
  }

  &:active {
    background-color: $special-blue-2;
    color: $special-white;
  }

  &:disabled {
    background-color: $special-grey-3;
  }

  @media (min-width: $tablet-width) {
    display: flex;
    justify-content: center;
    width: 324px;
    margin: 0 163px 65px 57px;
  }

  @media (min-width: $desktop-width) {
    width: 480px;
    margin: 0;
  }
}

.form__inner {
  @media (min-width: $tablet-width) {
    display: flex;
  }

  @media (min-width: $desktop-width) {
    display: flex;
    justify-content: space-between;
    margin: 52px 120px;
  }
}

.form__fieldset {
  border: none;
  margin: 0;
  padding: 0;

  @include reset-list;

  @media (max-width: $mobile-width-only) {
    border-bottom: 1px solid $special-grey-3;

    &--no-border {
      border: none;
    }
  }

  @media (min-width: $tablet-width) {
    &--first {
      order: 0;
      text-align: center;
    }

    &--second {
      order: 1;
      text-align: center;
      position: relative;

      &::after {
        content: "";
        position: absolute;
        height: 292px;
        width: 1px;
        background: $special-grey-5;
        bottom: 17px;
        left: 0;

        @media (min-width: $desktop-width) {
          bottom: 32px;
        }
      }
    }

    &--three {
      order: 2;
      grid-column: span 2;
    }
  }
}

.form__fieldset--first {
  padding-bottom: 10px;

  @media (min-width: $desktop-width) {
    text-align: start;
  }
}

.form__login {
  display: flex;
  padding: 0 20px 21px 22px;
  align-items: center;

  @media (min-width: $tablet-width) {
    padding: 1px 54px 19px 58px;
  }

  @media (min-width: $desktop-width) {
    padding: 0 0 20px 79px;
  }
}

.form__text {
  padding: 14px 0 10px 22px;
  box-sizing: border-box;

  &--login {
    width: 100%;
  }

  &--tel {
    @media (min-width: $desktop-width) {
      width: 317px;
      min-height: 58px;
    }
  }

  &--email {
    @media (max-width: $mobile-width-only) {
      margin-bottom: 28px;
    }

    @media (min-width: $desktop-width) {
      width: 316px;
    }
  }
}

.form__icon {
  position: absolute;
  right: 20px;
  transform: translateY(-50%);
  background-color: $special-grey-8;
}

.form__icon--tel {
  padding: 21px;
  top: 70%;

  @media (min-width: $tablet-width) {
    right: 60px;
  }

  @media (min-width: $desktop-width) {
    right: 0;
    top: 50%;
    padding-top: 20px;
    padding-right: 22px;
    padding-bottom: 21px;
    padding-left: 22px;
  }
}

.form__icon--email {
  padding-top: 23px;
  padding-right: 19px;
  padding-bottom: 25px;
  padding-left: 18px;
  top: 56%;

  @media (min-width: $tablet-width) {
    padding: 23px 19px;
    right: 60px;
    top: 75%;
  }

  @media (min-width: $desktop-width) {
    right: 0;
    top: 50%;
    padding: 23px 24px;
  }
}

.form__heading--contact,
.form__heading--lion,
.form__heading--comments {
  @media (max-width: $mobile-width-only) {
    padding-top: 22px;
    padding-bottom: 5px;
  }

  @media (min-width: $tablet-width) {
    position: relative;

    &::before,
    &::after {
      content: "";
      position: absolute;
      height: 1px;
      width: 123px;
      background: $special-grey-5;
      top: 52px;
    }

    &::before {
      right: 343px;
    }

    &::after {
      left: 343px;
    }
  }

  @media (min-width: $desktop-width) {
    &::before,
    &::after {
      width: 280px;
      top: 35px;
    }

    &::before {
      right: 328px;
      top: 35px;
    }

    &::after {
      left: 323px;
    }
  }
}

.form__heading--lion {
  @media (max-width: $mobile-width-only) {
    padding-top: 20px;
    padding-bottom: 19px;
  }

  @media (min-width: $tablet-width) {
    padding-top: 50px;
    padding-bottom: 44px;

    &::before {
      top: 77px;
    }

    &::after {
      top: 77px;
    }
  }

  @media (min-width: $desktop-width) {
    &::before {
      right: 339px;
      top: 77px;
    }

    &::after {
      top: 77px;
    }
  }
}

.form__heading--contact {
  @media (min-width: $tablet-width) {
    padding-top: 43px;
    padding-bottom: 10px;
  }

  @media (min-width: $desktop-width) {
    padding-top: 40px;
    padding-bottom: 71px;
    padding-right: 6px;

    &::before {
      top: 47px;
      right: 347px;
    }

    &::after {
      top: 47px;
    }
  }
}


.form__heading--comments {
  padding-left: 7px;
  margin-bottom: 10px;
  padding-top: 11px;

  @media (min-width: $tablet-width) {
    padding-top: 41px;
    padding-bottom: 54px;
    padding-left: 2px;
    margin-bottom: 1px;
  }

  @media (min-width: $desktop-width) {
    padding-top: 35px;
    padding-bottom: 0;

    &::before {
      right: 327px;
      top: 46px;
    }

    &::after {
      top: 46px;
      left: 327px;
    }
  }
}

.form__contacts {
  display: flex;
  flex-direction: column;
  position: relative;

  @media (min-width: $desktop-width) {
    display: flex;
    flex-direction: row;
    align-items: center;
  }
}

.form__row {
  @media (min-width: $desktop-width) {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    margin-left: 5px;
  }
}

.form__desc {
  width: 235px;
  min-height: 160px;
  padding: 8px 23px 11px 23px;
  margin: 0 22px 17px 20px;

  @media (min-width: $tablet-width) {
    min-width: 606px;
    min-height: 118px;
    padding: 14px 21px 20px 21px;
    margin: 0 60px 50px;
  }

  @media (min-width: $desktop-width) {
    box-sizing: border-box;
    min-width: 960px;
    min-height: 160px;
    margin: 55px 120px 0 120px;
  }
}

.form__holder {
  @media (min-width: $tablet-width) {
    display: grid;
    grid-template-columns: 1fr 1fr;
  }

  @media (min-width: $desktop-width) {
    padding-top: 262px;
    padding-left: 112px;
    padding-right: 114px;
  }
}

.form__wrap {
  @media (max-width: $mobile-width-only) {
    padding-left: 23px;
    padding-bottom: 18px;
  }

  @media (min-width: $tablet-width) {
    display: grid;
    grid-template-columns: 1fr 1fr;
    max-width: 472px;
    margin-left: 154px;
    gap: 26px 49px;
    padding-bottom: 24px;
  }

  @media (min-width: $desktop-width) {
    display: grid;
    grid-template-columns: repeat(4, min-content);
    margin-left: 121px;
    margin-right: 40px;
    max-width: none;
    gap: 74px;
    white-space: nowrap;
  }
}

.form__legend {
  @include font-Text-large;
  color: $basic-grey;

  @media (max-width: $mobile-width-only) {
    display: none;
  }

  @media (min-width: $tablet-width) {
    margin-top: 20px;
    margin-left: 5px;
  }
}

.form__lions {
  @media (max-width: $mobile-width-only) {
    margin-bottom: 16px;
  }
}

.form__lions .form__grade {
  padding-top: 0;
}

.form__effect {
  padding-bottom: 5px;
  padding-left: 19px;

  @media (min-width: $tablet-width) {
    padding-bottom: 14px;
    padding-left: 0;
  }

  @media (min-width: $desktop-width) {
    padding-bottom: 13px;
  }
}
